@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap");

// **************General *************//
html,body {
    width: 100%;
    height: 100%;
    word-wrap: break-word;
    color: #354459;
    min-width: 347px;
    font-size: 15px;
    background: #242628;
    background-size: cover;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    font-family: 'Poppins', sans-serif;
    font-weight: 500;
    overflow-x: hidden; }

* {
    box-sizing: border-box; }

.profile,.main {
    min-height: 748.5px;
    border-radius: 7px;
    border-top: 5px solid#607a9f;
    padding: 0;
    background: white; }

//****************PROFILE*******************************//
.profile {
    .name {
        width: 100%;
        display: inline-block;
        margin: 0.5rem auto 1rem auto;
        font-weight: bold;
        font-size: 2rem; }

    .name:after {
        background: #414040be;
        content: '';
        display: block;
        width: 10rem;
        height: 2px;
        margin: 0.5rem auto;
        border-radius: 1px;
        justify-content: center; }

    .title {
        background: #cfd8dc;
        border: none;
        color: #37474f;
        padding: 0.7rem 1.5rem;
        margin-top: 1rem; }

    .intro {
        color: #455a64;
        text-align: left;
        margin: 1rem 0;
        padding: 1rem; }

    .contact {
        background: #eceff1;
        width: 100%;
        padding: 1.5rem 0 1.5rem 2rem;
        margin-top: 1rem;
        color: #546e7a;
        text-align: left;

        a, a:after {
            color: #546e7a; }
        a:hover {
            text-decoration: none; }

        i {
            font-size: 1.2rem;
            margin: 0.35rem auto;
            width: 19.2px; } }

    .email {
        margin-top: 4rem;
        margin-bottom: 2rem;
        background-color: #607a9f;
        border: none;
        color: white; }

    .email:hover {
        background-color: #4d6280;
        color: white; } }

//****************MAIN*******************************//
.main {
    padding: 1rem 1.5rem 5rem 1.5rem;
    .link {
        background-color: #2196f3;
        border: none;
        padding: 0.2rem 1.5rem 0.4rem 1.5rem;
        color: white;
        float: right;
        font-size: 1.6rem;
        transition: .5s;

        i {
            font-size: 1.3rem; } }

    .link:hover,.link:active,.link:focus {
        box-shadow: 0 0 4px #2196f3,0 0 8px #2196f3,0 0 12px #2196f3;

        i {
            animation: link-animation .75s; } }

    .title {
        font-size: 2rem;
        font-weight: bold;
        margin-bottom: 1rem; }

    .title:after {
        background: #414040be;
        content: '';
        display: block;
        width: 10%;
        height: 2px;
        margin-top: 0.3rem;
        border-radius: 1px; }

    .subtitle {
        font-size: 1.7rem;
        font-weight: bold; }

    .resume-box {
        min-height: 150px; }

    .resume-content {
        padding: 1rem;
        color: #5d697a;

        .content-title {
            font-size: 1.2rem;
            font-weight: bold;
            margin-bottom: 0.5rem; }

        .duty {
            display: inline-block;
            margin-bottom: 0.3rem; }

        .tech-btn {
            display: inline-block;
            background-color: #607a9f;
            color: white;
            margin: 0.25rem;
            padding: 0.3rem 0.8rem; } }

    .card { //portfolioCard
        margin: 0;
        .imgcontainer {
            height: 100px;
            background: white;
            overflow: hidden; }
        .portfolio-lang {
            display: inline-block;
            background-color: #607a9f;
            color: white;
            margin: 0.1rem 0.5rem 0.75rem 0;
            padding: 0.2rem 0.3rem;
            font-size: .95rem; }
        .card-body {
            padding: 1rem; }
        .card-text {
            margin-bottom: 0.75rem;
            font-size: .95rem; } } }

.pagination-container {
    position: absolute;
    margin-top: 1rem;
    position: absolute;
    bottom: .8rem;
    right: .8rem; }

.pagination {
    cursor: pointer;

    li:not(:first-child) {
        margin-left: .5rem; }
    & a {
        padding: .5rem 1rem;
        border-radius: 5px;
        border: 1px solid #6c7ac9;
        &:hover,&:focus,&:active {
            outline: none; } }

    &-link {
        font-weight: bold; }

    &-link-active a {
        color: #fff;
        background-color: #2196f3; }

    &-link-disabled a {
        color: rgb(198, 197, 202);
        border: 1px solid rgb(198, 197, 202); } }

//****************RESPONSIVE DESIGN*******************************//
@media screen and (max-width: 1199px) {
    .main,.profile {
        min-height: 892px; }

    .profile {
        .title {
            padding: 0.5rem; }
        .contact {
            padding-left: 1.5rem; } } }

@media screen and (max-width: 991px) {
    .mobile-fix {
        padding-left: 0 !important; }

    .row.my-auto {
        margin-top: 0 !important; }

    .main {
        padding-bottom: 4rem; }
    .main,.profile {
        margin-left: 0;
        margin-top: 1rem;
        min-height: 0; }
    .profile {
        .email {
            margin-top: 2rem; }
        .title {
            padding: 0.5rem 1rem; }
        .contact {
            padding-left: 2rem; } }
    .main {
        margin-left: auto;
        margin-bottom: 1rem;
        .resume-box {
            min-height: 120px; }

        .card {
            .imgcontainer {
                height: 150px; } }

        .pagination-container {
            bottom: 1.2rem; } } }

@keyframes link-animation {
    0% {
        transform: rotate(0deg); }
    25% {
        transform: rotate(30deg); }
    75% {
        transform: rotate(-30deg); }
    100% {
        transform: rotate(0deg); } }
